.sidebar-row{
    display: flex;
    align-items: center;
    padding: 10px 20px;
}
.sidebar-row-icon{
    color: #606060;
    font-size: large !important;
}
.sidebar-row-title{
    flex:1;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;
}
.sidebar-row:hover{
    background: lightgray;
    cursor: pointer;
}
.sidebar-row:hover > .sidebar-row-title{
    font-weight: bold;
}
.sidebar-row.selected{
    background: lightgray;
}
.sidebar-row.selected > .sidebar-row-icon{
    color: red;
}
.sidebar-row.selected > .sidebar-row-title{
    font-weight: bold;
}

@media screen and (max-width: 650px){
    .sidebar-row-title{
        display: none;
        flex: 0.2;
    }
}